@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
*{margin: 0; padding: 0; box-sizing: border-box;font-family: 'Open Sans', sans-serif;}
body{ background-color: #f39f86;
  background-image: linear-gradient(315deg, #f39f86 0%, #f9d976 74%);
  display: -webkit-flex;
  display: flex; -webkit-align-items: center; align-items: center; -webkit-justify-content: center; justify-content: center; height: 100vh;font-family: 'Poppins', sans-serif;}
.App{width: 560px; display: -webkit-flex; display: flex; -webkit-justify-content: center; justify-content: center; -webkit-align-items: center; align-items: center; -webkit-flex-direction: column; flex-direction: column;}
.App .header{
  width: 100%; background-color: #f55c47; margin-bottom: 30px;
  padding: 20px 30px; 
}
.header h1{
  color:#fff; text-align: center; font-size: 45px; letter-spacing: 2px; 
}
.App form{
  height: 50px;
  display: -webkit-flex;
  display: flex; -webkit-justify-content: center; justify-content: center; -webkit-align-items: center; align-items: center; margin-bottom: 30px;
  
}
form input,
form .plus{padding:10px 20px; height: 100%; border:none; outline:0;font-size: 25px; }

form input{min-width: 500px; }
form .plus{display: -webkit-flex;display: flex; -webkit-align-items: center; align-items: center; background:#fff; color:#f55c47; cursor:pointer}

.shop-list{
  
  display: -webkit-flex;
  
  display: flex; -webkit-align-items: center; align-items: center; -webkit-flex-direction: column; flex-direction: column; 
}
.shop-list ul{
  display: -webkit-flex;
  display: flex; -webkit-align-items: center; align-items: center; -webkit-justify-content: center; justify-content: center;
  max-height: 60px; margin:10px;
}
.shop-list ul li{
  list-style: none; font-size: 25px; background:#fff;
  min-width: 310px; display: -webkit-flex; display: flex; -webkit-justify-content: center; justify-content: center; -webkit-align-items: center; align-items: center;
  height: 50px; padding:10px 20px;
  
;}

.btnCheck,.btnTrash{height: 50px; min-width: 50px;font-size: 30px; border:none; outline:none;}
.btnCheck{background: rgb(11, 212, 162); color:#fff;}
.btnTrash{background:#f55c47; color:#fff;}
.completed {
  text-decoration: line-through;
  opacity: 0.5;
}
